<template>
  <div>
    <Header name="忘记密码"></Header>
    <div class="block">
      <van-form @submit="onSubmit">
        <img
          :src="require('@/assets/image/shouji.png')"
          alt=""
          class="phone"
        />
        <van-field
          v-model="form.phone"
          name="用户名"
          label=""
          placeholder="请输入手机号码"
          :rules="[{ required: true }]"
        />
        <img
          :src="require('@/assets/image/yzm.png')"
          alt=""
          class="password"
          style="width: 19px; height: 21px; margin-top: 18px; left: 18px"
        />
        <van-field
          v-model="form.verificationCode"
          center
          clearable
          label=""
          placeholder="请输入验证码"
          :rules="[{ required: true }]"
        >
          <template #button>
            <span @click="getNumber" v-if="countDown" style="width: 19px; height: 21px; margin-top: 22px; left: 18px; color: #999999;"><span style="color: #D6D6D6; margin-right: 15px;">|</span>获取验证码</span>
            <span  v-else style="width: 19px; height: 21px; margin-top: 22px; left: 18px; color: #999999;"><span style="color: #D6D6D6; margin-right: 15px;">|</span>{{time}}</span>
          </template>
        </van-field>
        <img
          :src="require('@/assets/image/mima.png')"
          alt=""
          class="password"
        />
        <van-field
          v-model="form.password"
          type="password"
          name="密码"
          label=""
          placeholder="请输入8-16位数字或字母组成的密码"
          :rules="[{ required: true }, {validator, message: '请输入8-16位数字或字母组成的密码'}]"
        />
        <img
          :src="require('@/assets/image/mima.png')"
          alt=""
          class="password"
        />
        <van-field
          v-model="form.passwords"
          type="password"
          name="密码"
          label=""
          placeholder="请再次输入密码"
          :rules="[{ required: true }, {validator, message: '请输入8-16位数字或字母组成的密码'}]"
        />
        <div style="margin: 46px 6.6% 0px">
          <van-button color="#ff4c4c" block type="info" native-type="submit"
            >确认</van-button
          >
        </div>
        <div class="tologin">
          已有帐号&nbsp;&nbsp;<span @click="tologin">立即登录</span>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'

export default {
  components :{
    Header
  },
  data() {
    return {
      countDown:true,
      time:60,
      form: {
        phone: '',
        verificationCode: '',
        password:'',
        passwords: ''
      },
      //请输入8-16位数字或字母组成的密码
      validator(val) {
        var len = val.length;
        if( len > 7 && len < 17) {
          return true
        }else {
          return false
        }
      }
    };
  },
  methods: {
    onSubmit () {
      let me = this
      if (me.form.password === me.form.passwords) {
        api.findPassword(me.form).then(res => {
          if (res.code === 200) {
            me.$toast.success('操作成功，正在前往登录！');
            me.$nextTick(()=> {
              me.$router.push({
                path:'/user/login/index',
              })
            }, 1000)
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        })
      } else {
        me.$notify({
          message: "两次密码不一致",
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    tologin () {
      this.$router.push({
        path:'/user/login/index'
      })
    },
    // 验证码
    getNumber () {
      let me = this
      if (me.form.phone) {
        api.getVerificationCode({'phone' : me.form.phone, 'register': false}).then(res => {
          if (res.code === 200) {
            me.$notify({
              type: 'success',
              message: res.message
            });
            me.countDown = false
            let timer = setInterval(()=>{
              me.time--
              if(me.time === 0){
                me.countDown = true
                me.time = 60
                clearInterval(timer)
              }
            },1000)
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        })
      } else {
        me.$notify({
          message: "手机号码不能为空",
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    }
  },
};
</script>

<style lang="stylus" scoped>
.van-button__text {
  font-size: 16px;
}
.block {
  position: relative;
  width: 100%;
  height: 715px;
  background: #fff;
  padding-top: 24px;

  >>>.van-field__label {
    width: 43.4%;
  }
  >>>.van-cell {
    padding: 16px 16px;
    border-bottom: 1px solid #ECECEC;
    width: 90%;
    margin-left: 5%;
  }

  >>>.van-field__control {
    padding-left: 15px;
  }
  .tologin {
    position: absolute;
    left: 16.53%;
    width: 67.2%;
    font-size: 13px;
    color: #888888;
    text-align: center;
    margin-top: 43px;

    span {
      color: #FD2744;
    }
  }

  .password {
    width: 17px;
    height: 19px;
    position: absolute;
    z-index: 1;
    left: 20px;
    margin-top: 17px;
  }

  .phone {
    width: 13px;
    height: 20px;
    position: absolute;
    z-index: 1;
    left: 20px;
    margin-top: 18px;
  }
  .next{
    display flex
    justify-content space-between
    >>>.van-button{
      width 43%
    }
  }
}
</style>


